<template>
  <div class="password-wrapper">
    <div class="center_block">
      <Nav />
      <div class="content">
        <div class="title-wrapper">
          <span>账号安全</span>
        </div>
        <div class="body-wrapper">
          <div class="title">
            <span>设置密码</span>
          </div>
          <el-form
            :model="ruleForm"
            status-icon
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="密码" prop="pass">
              <el-input
                type="password"
                v-model="ruleForm.pass"
                autocomplete="off"
                placeholder="请输入新密码(数字、特殊字符、字母)"
              ></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkPass">
              <el-input
                type="password"
                v-model="ruleForm.checkPass"
                autocomplete="off"
                placeholder="请再次输入新的密码"
              ></el-input>
            </el-form-item>
            <el-form-item label="图形验证码" prop="graphicsCode">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input placeholder="请输入图形验证码" v-model="ruleForm.graphicsCode"></el-input>
                </div>
                <div
                  class="login-code"
                  @click="refreshCode"
                  style="width:120px;height:40px;border: 1px solid #d7dae2;border-radius: 4px;overflow: hidden;box-sizing:border-box;"
                >
                  <el-image style="width:100%;height:100%;" :src="refresh.b64s" lazy></el-image>
                </div>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="item-wrapper">
                <el-button type="warning" @click="submitForm('ruleForm')">确定</el-button>
                <!-- <el-button @click="$router.push({path:'account',query:{name:'security'}})">返回</el-button> -->
                <el-button @click="$router.push('/')">返回</el-button>
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav";
import { captcha } from "@/api/code";
import { setPassword } from "@/api/user";
import { getCode, setEncryption, artificial_code } from "@/mixins/user";
import { isPassWord } from "@/utils/validate";
export default {
  components: {
    Nav,
  },
  mixins: [getCode, setEncryption, artificial_code],
  props: {},
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else if (!isPassWord(value)) {
        callback(new Error("密码不符合规则"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    const validateSidentify = (rule, value, callback) => {
      if (!value) return callback(new Error("图形验证码不能为空"));
      this.valideCode(value, callback);
    };
    return {
      phone: this.$route.params.phone,
      refresh: {
        b64s: "",
        id: ""
      },
      PUBLIC_KEY: null,
      ruleForm: {
        pass: "",
        checkPass: "",
        graphicsCode: "",
      },
      rules: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        graphicsCode: [{ validator: validateSidentify, trigger: "blur" }],
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    valideCode(value, callback) {
      captcha(
        JSON.stringify({
          id: this.refresh.id,
          value: this.ruleForm.graphicsCode,
        })
      ).then((response) => {
        if (!response.data.verify_status) {
          this.refreshCode();
          return callback(new Error("图形验证码错误"));
        }
        callback();
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let _this = this;
          let obj = JSON.stringify({
            phone_num: this.phone,
            password: this.encryption(this.ruleForm.pass),
          });
          setPassword(obj).then((res) => {
            _this.$message({
              message: "设置成功!跳转中",
              type: "success",
            });
            _this.$store.dispatch("getAgent_token")
            .catch(error => {
              _this.$router.push({ name: 'account' })
            })
          });
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/pc/index";
.password-wrapper {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
}
</style>


